import { Button, DatePicker, Divider, Form, Input, InputNumber, Popconfirm, Select, Steps, Table, Typography } from 'antd';
import { APP_PREFIX_PATH } from 'configs/AppConfig';
import { DATE_FORMAT } from 'constants/CommonConstants';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
const originData = [];
for (let i = 0; i < 5; i++) {
  originData.push({
    employeeId: `22970${i.toString()}`,
    ReleaseStartDate: ``,
    Reason: '',
    Feedback: ``,
  });
}
const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};
const BulkAllocate = () => {
  const [form] = Form.useForm();
  const [data, setData] = useState(originData);
  const [editingKey, setEditingKey] = useState('');
  const isEditing = (record) => record.key === editingKey;
  const edit = (record) => {
    form.setFieldsValue({
      name: '',
      age: '',
      address: '',
      ...record,
    });
    setEditingKey(record.key);
  };
  const cancel = () => {
    setEditingKey('');
  };
  const save = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...data];
      const index = newData.findIndex((item) => key === item.key);
      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, {
          ...item,
          ...row,
        });
        setData(newData);
        setEditingKey('');
      } else {
        newData.push(row);
        setData(newData);
        setEditingKey('');
      }
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };
  const columns = [
    {
      title: 'employeeId',
      dataIndex: 'employeeId',
      width: '15%',
      editable: false,
    },
    {
      title: 'Realease Start Date',
      dataIndex: 'releaseStartDate',
      width: '15%',
      editable: true,
      render: (_, record) => {
        return (
          <span>
             <DatePicker
                format={DATE_FORMAT}
                // value={s[0]}
                onChange={e => {
                    console.log([e]);
                }}
                onPressEnter={() => console.log()}
                className="mb-2 d-block"
                allowClear={true}
                // ref={node => (ref = node)}
                />
          </span>
        )
      },
     
    },
    {
      title: 'Reason',
      dataIndex: 'Reason',
      width: '20%',
      render: (_, record) => {
        return (
          <span>
            <Select
                defaultValue="Terminate"
                style={{ width: `100%` }}
                placeholder="Please Select"
                options={[
                    {
                    value: 'Terminate',
                    label: 'Terminate',
                    },
                    {
                    value: 'Dual Employeement',
                    label: 'Dual Employeement',
                    },
                    {
                    value: 'Feedback',
                    label: 'Feedback',
                    },
                    {
                    value: 'Test',
                    label: 'Test',
                    },
                    {
                    value: 'Resign',
                    label: 'Resign',
                    },
                ]}
                />
          </span>
        )
      },
    },
    {
      title: 'Feedback',
      dataIndex: 'Feedback',
      render: (_, record) => {
        return (
          <span>
            <Input type="text" placeholder='Feedback' />
          </span>
        )
      },
    },
  ];
  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }
    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'age' ? 'number' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        // editing: isEditing(record),
      }),
    };
  });
  const { Step } = Steps;
  return (
    <React.Fragment>
      <Steps current={1}>
      <Step title="Step 1" description="Select Employee" />
      <Step title="Step 2" description="Bulk Allocate" />
      </Steps>
      <Button className="mt-3">
        <Link to={`${APP_PREFIX_PATH}/manage/move`} >
        Previous
        </Link>
      </Button>

    <Divider/>
      <Form form={form} component={false}>
        <Table
          bordered
          dataSource={data}
          columns={mergedColumns}
          rowClassName="editable-row"
          pagination={{
            onChange: cancel,
          }}
        />
      </Form>
    </React.Fragment>
  );
};
export default BulkAllocate;